.project {
    position: relative;
    width: 450px;
    border-radius: 25px;
    margin: 25px;
}
.project img {
    opacity: 1;
    width: 100%;
    height: 100%;
    -webkit-transition: .5s ease;
    -o-transition: .5s ease;
    transition: .5s ease;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    border-radius: 10px;
}
.projectoverlay {
    -webkit-transition: .5s ease;
    -o-transition: .5s ease;
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    -webkit-transform: translate(0,0);
        -ms-transform: translate(0,0);
            transform: translate(0,0);
    text-align: center;
    color: black;
    background: #f4f0f0;
}

.projectoverlay h2 {
    padding-top: 10%;
}

.projectoverlay p {
    padding-bottom: 30%;
}

.project:hover img {
    opacity: 0.3;
}

.project:hover .projectoverlay {
    opacity: 1;
}

.learnmore {
    border: 3px solid #d8364c;
    margin:0 30%;
    -webkit-transition: .5s ease;
    -o-transition: .5s ease;
    transition: .5s ease;
    bottom: 10px;
    cursor: pointer;
}

.learnmore:hover {
    background: #d8364c;
    color: white;
    -webkit-transition: .5s ease;
    -o-transition: .5s ease;
    transition: .5s ease;
}

@media (max-width: 1500px){
    .project {
        width: 375px;
        margin: 12px;
    }
}

@media (max-width: 1200px) {
    .project {
        width: 244px;
        margin: 6px;
    }
    .projectoverlay h2 {
        padding-top: 5%;
        font-size: 1.1rem;
    }
    .projectoverlay p {
        font-size: 0.7rem;
        padding-bottom: 30%;
    }

    .learnmore {
        border: 3px solid #d8364c;
        margin:0 25%;
        -webkit-transition: .5s ease;
        -o-transition: .5s ease;
        transition: .5s ease;
        bottom: 10px;
        font-size: .8rem;
    }

}
@media (max-width: 768px){
    .project {
        width: 80%;
        margin: 5px auto;
    }
    .projectoverlay h2 {
        margin-top: 10%;
        font-size: 2rem;
    }
    
    .projectoverlay p {
        font-size: 1.2rem;
        padding-bottom: 30%;
    }
    .learnmore {
        border: 3px solid #d8364c;
        margin:0 30%;
        -webkit-transition: .5s ease;
        -o-transition: .5s ease;
        transition: .5s ease;
        bottom: 10px;
        font-size: 1.4rem;
    }
}

@media (max-width: 460px) {
    .projectoverlay h2 {
        padding-top: 10%;
        font-size: 1.2rem;
    }
    
    .projectoverlay p {
        font-size: 0.8rem;
        padding-bottom: 27.5%;
    }
    .learnmore {
        border: 2px solid #d8364c;
        margin:0 30%;
        -webkit-transition: .5s ease;
        -o-transition: .5s ease;
        transition: .5s ease;
        bottom: 10px;
        font-size: 0.9rem;
    }
}

@media (max-width: 375px) {
    .projectoverlay h2 {
        padding-top: 2.5%;
        font-size: 1.2rem;
    }
    
    .projectoverlay p {
        font-size: 0.8rem;
        padding-bottom: 27.5%;
    }
    .learnmore {
        border: 2px solid #d8364c;
        margin:0 30%;
        -webkit-transition: .5s ease;
        -o-transition: .5s ease;
        transition: .5s ease;
        bottom: 10px;
        font-size: 0.9rem;
    }
}


