.portfolio {
    height: auto;
    background: #f4f0f0;
    width: 100%;
    padding-top: 200px;
    padding-bottom: 200px;
}


.porthead {
    margin: 0 auto;
    width: 150px;
    font-size: 2.5rem;
    color: black;
    opacity: 1;
    -webkit-animation: portfo 0.8s cubic-bezier(0, 0, 0, 1) 1 normal forwards;
            animation: portfo 0.8s cubic-bezier(0, 0, 0, 1) 1 normal forwards;
}

.porthead-clear {
    opacity: 0;
    font-size: 2.5rem;
}

.underline-clear {
    height: 5px;
    width: 80px;
    opacity: 0;
}

.portfoliounderline {
    height: 5px;
    background: #d8364c;
    width: 80px;
    margin: 0 auto;
    opacity: 0;
    -webkit-animation: portfo 0.8s cubic-bezier(0, 0, 0, 1) 1 normal forwards;
            animation: portfo 0.8s cubic-bezier(0, 0, 0, 1) 1 normal forwards;
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
}
.portfolioslides {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-direction: row;
        flex-direction: row;
    width: 1500px;
    margin: 100px auto;
    height:auto;
    -webkit-animation: slidein 1s cubic-bezier(0, 0, 0, 1) 1 normal forwards;
            animation: slidein 1s cubic-bezier(0, 0, 0, 1) 1 normal forwards;
}

.slides-clear {
    padding-top: 100px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-direction: row;
        flex-direction: row;
    width: 1500px;
    margin: 0 auto;
    height:auto;
    opacity: 0;
}

@-webkit-keyframes portfo {
    0% {
        opacity: 0;
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@keyframes portfo {
    0% {
        opacity: 0;
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@-webkit-keyframes slidein {
    0%{
        opacity: 0;
        -webkit-transform: translateY(50%);
                transform: translateY(50%);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes slidein {
    0%{
        opacity: 0;
        -webkit-transform: translateY(50%);
                transform: translateY(50%);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@media (max-width: 1500px) {
    .portfolioslides {
        width: 1200px;
    }
    
    .slides-clear {
        width: 1200px;
    }
}


@media (max-width: 1200px) {
    .portfolioslides {
        width: 768px;
    }
    
    .slides-clear {
        width: 768px;
    }
}

@media (max-width: 768px){
    .portfolioslides {
        width: 90%;
        display: grid;
    }
}

@media (max-width: 420px){
    .portfoliotitle h2 {
        font-size: 2rem;
    }
    .portfolio {
        padding-top: 150px;
        padding-bottom: 150px;
    }
}

@media (max-width: 375px){
    .portfoliotitle h2 {
        font-size: 1.8rem;
    }
    
}


