.drawer {
    width: 100%;
    z-index: 500;
    height: auto;
    top: 0;
    left: 0;
    opacity: 0;
    position: fixed;
    -webkit-transform: translateX(-100%);
        -ms-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: all 0.3s ease-in;
    -o-transition: all 0.3s ease-in;
    transition: all 0.3s ease-in;
    background: #393e46;
    border-radius: 0 0 25px 25px;
    COLOR: WHITE;
    font-size: 1.5rem;
}

.drawer.open {
    -webkit-transform: translateX(0);
        -ms-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
}

.drawerNav {
    margin: 0 auto;
}

.drawerNav ul {
    list-style: none;
    padding-bottom: 50px;
    margin: 0 auto;
}
.drawerspan {
    display: -ms-flexbox;
    display: flex;
    margin-top: 20px;
    margin-right: 20px;
}
.drawerspan i {
    color: white;
}

.drawerNav li {
    margin: 10px auto;
    cursor: pointer;
    width: 120px;
    padding-bottom: 10px;
}


