.about {
    height: auto;
    background: #fff;
    width: 100%;
    padding-top: 200px;
    padding-bottom: 200px;
}

.about-details {
    opacity: 0;
    -webkit-transition: 0.7s ease-in;
    -o-transition: 0.7s ease-in;
    transition: 0.7s ease-in
}

.about-details.animate {
    opacity: 1;
    -webkit-transition: 0.7s ease-in;
    -o-transition: 0.7s ease-in;
    transition: 0.7s ease-in;
}
@media (max-width: 420px){
    .about {
        padding-top: 150px;
        padding-bottom: 150px;
    }
    
}


